import React  from "react";
import TextField from "material-ui/TextField";
import RaisedButton from "material-ui/RaisedButton";   

export default class Form extends React.Component {
  state = {
    invoiceName: "",
    invoiceNameError: "",
    invoiceDesc: "",
    invoiceDescError: "",
    invoiceNo: "",
    invoiceNoError: "" 
  };

  change = e => { 
    this.setState({
      [e.target.name]: e.target.value
    });
  };
  

  validate = () => {
    let isError = false;
    const errors = {
      invoiceNameError: "",
      invoiceDescError: "",
      invoiceNoError: "" 
    }; 

    if (this.state.invoiceName.trim().length == 0) {
      isError = true;
      errors.invoiceNameError = "Fatura adı boş geçilemez";
    }

    if (this.state.invoiceDesc.trim().length == 0) {
      isError = true;
      errors.invoiceDescError = "Fatura açıklaması boş geçilemez";
    }

    if (this.state.invoiceNo.trim().length == 0) {
      isError = true;
      errors.invoiceNoError = "Fatura no boş geçilemez";
    }
    
    this.setState({
      ...this.state,
      ...errors
    });

    return isError;
  };

  onSubmit = e => {
    e.preventDefault();
    const err = this.validate();
    if (!err) {
      this.props.onSubmit(this.state);
      // clear form
      this.setState({
        invoiceName: "",
        invoiceNameError: "",
        invoiceDesc: "",
        invoiceDescError: "",
        invoiceNo: "",
        invoiceNoError: "" 
      });
    }
  };

  render() {  
    return (
      <form>
        <div className="txtFrame ml_10"> 
            <TextField  className="textInputFrame"
              name="invoiceName"
              hintText="Fatura Adı"
              floatingLabelText="Fatura Adı"
              value={this.state.invoiceName}
              onChange={e => this.change(e)}
              errorText={this.state.invoiceNameError}
              floatingLabelFixed    
            /> 
        </div>

        
        <div className="txtFrame ml_10"> 
            <TextField  className="textInputFrame"
              name="invoiceDesc"
              hintText="Fatura Açıklaması"
              floatingLabelText="Fatura Açıklaması"
              value={this.state.invoiceDesc}
              onChange={e => this.change(e)}
              errorText={this.state.invoiceDescError}
              floatingLabelFixed  
            /> 
        </div>
          
        <div className="txtFrame ml_10">   
            <TextField className="textInputFrame"
              name="invoiceNo"
              hintText="Fatura No"
              floatingLabelText="Fatura No"
              value={this.state.invoiceNo}
              onChange={e => this.change(e)}
              errorText={this.state.invoiceNoError}
              floatingLabelFixed  
            /> 
          </div> 
            
        <div className="txtFrame ml_10">   
            <RaisedButton  className="btnFrame" label="KAYDET" onClick={e => this.onSubmit(e)} primary />
        </div>
      </form>
    );
  }
}



